@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@400;500;600&display=swap');
* {
  box-sizing: border-box;
  color: #000;
  font-family: 'Kanit', sans-serif;
}
.app-container {
  width: 500px;
  margin: 0 auto;
  background: #fff5f0;
  box-shadow:  20px 20px 60px #d9d0cc,
             -20px -20px 60px #ffffff;
  border-radius: 12px;
  padding: 50px 20px;
}
h1 {
  margin-top: 0px;
  margin-bottom: 25px;
  font-weight: 400;
}
input {
  width: 100%;
  padding: 20px 15px;
  background: #0000000f;
  border: none;
  border-radius: 12px;
  outline: none;
  color: #2d2d2d;
}
body {
  background: #fff5f0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.addbtn {
  width: 180px;
  height: 50px;
  background-color: #ffcfa7;
  border: none;
  padding: 10px;
  font-size: 18px;
  border-radius: 12px;
  box-shadow: 10px 10px 14px 1px rgb(0 0 0 / 20%);
  letter-spacing: 0.094em;
  border-radius: 50px;
  margin: 20px 0px;
  cursor: pointer;
  color: #000;
  transition: all .4s;
}
.addbtn:hover {
  background-color: #000;
  color: #fff;
  box-shadow: none;
}
button {
  border: none;
  background-color: transparent;
}
ul {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

ul > div {
  padding: 15px;
  background-color: #fff;
  margin-bottom: 10px;
}
ul input {
  margin-top: 15px;
}